html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow-left {
  width: 0.64em;
  height: 1em;
  fill: initial;
}

.icon-dots {
  width: 3.4em;
  height: 1em;
  fill: #000;
}

.icon-edit {
  width: 1em;
  height: 1em;
  fill: #fff;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 0.63em;
  height: 1em;
  fill: #fff;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: url(#a);
}

.out:after {
  content: " ";
  display: table;
  clear: both;
}

body {
  min-width: 1100px;
}

.container {
  padding: 0 35px;
  margin: 0 auto;
  max-width: 1415px;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

.main {
  padding-left: 200px;
  background-color: #f2f2f2;
}

.controls {
  display: inline-block;
  width: 100%;
  margin: 26px 0 34px;
}

.controls:after {
  content: " ";
  display: table;
  clear: both;
}

.controls__btn {
  background: #3D61FF;
  box-shadow: 0px 3px 7px rgba(61, 97, 255, 0.270211), inset 0px 1px 1px rgba(0, 0, 0, 0.110479);
  border-radius: 4px;
  font-family: Source Sans Pro;
  line-height: normal;
  font-weight: 600;
  font-size: 18px;
  text-align: center;
  letter-spacing: 0.168px;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 10px 38px;
  float: right;
}

.controls__btn:hover {
  color: #3C52C9;
  background-color: #fff;
}

.selected {
  margin-left: 20px;
  display: inline-block;
  vertical-align: middle;
}

.selected__counter, .selected__text {
  display: inline-block;
  vertical-align: middle;
  font-family: Source Sans Pro;
  font-size: 14px;
  letter-spacing: 0.130667px;
}

.selected__counter {
  color: #3C52C9;
  margin-right: 10px;
  font-style: normal;
  font-weight: bold;
  line-height: 14px;
  color: #3C52C9;
}

.selected__text {
  font-family: Source Sans Pro;
  font-weight: bold;
  line-height: normal;
  color: rgba(0, 0, 0, 0.365093);
}

.sidebar {
  width: 200px;
  height: 100vh;
  float: left;
  background-color: #3C52C9;
  background: radial-gradient(1613.51px at 50% 0%, #3C52C9 0%, #6B7DEA 100%);
}

.sidebar__container {
  padding: 17px 15px 20px 25px;
  height: 100%;
}

.sidebar__title, .sidebar__meta {
  color: #fff;
  display: inline-block;
  font-family: Source Sans Pro;
  font-style: normal;
  line-height: normal;
  text-align: justify;
}

.sidebar__title {
  font-size: 19px;
  font-weight: bold;
  margin-right: 5px;
}

.sidebar__meta {
  font-weight: 300;
  line-height: 1;
  font-size: 8px;
  text-transform: lowercase;
}

.sidebar__logo {
  padding-bottom: 17px;
}

.sidebar__logo-icon {
  margin-right: 14px;
}

.logo {
  line-height: 24px;
  display: inline-block;
  width: 24px;
  height: 24px;
  font-size: 0;
  text-align: center;
  vertical-align: bottom;
  border-radius: 50px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.148805);
  background-color: #3C92C9;
  background: radial-gradient(38.22px at 50% 0%, #6B6BEA 0%, #3C92C9 100%);
}

.logo__icon {
  vertical-align: middle;
  font-size: 15px;
  display: inline-block;
}

.header {
  background-color: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.0465919);
  padding: 15px 0;
  text-align: center;
  display: inline-block;
  width: 100%;
}

.header__btn {
  color: #3C52C9;
  display: inline-block;
  vertical-align: middle;
  float: left;
  border-radius: 3px;
  font-weight: bold;
  line-height: 13px;
  font-size: 12.6px;
  letter-spacing: 0.1176px;
  color: #3C52C9;
  padding: 8px 14px;
  border: 1px solid #3C52C9;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.110479);
}

.header__btn:hover {
  color: #fff;
  background-color: #3C52C9;
}

.header__btn .btn__icon {
  fill: none;
  stroke: currentColor;
  font-size: 8px;
}

.header__title {
  font-size: 16px;
  display: inline-block;
  vertical-align: middle;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  line-height: 1.93;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #6B7DEA;
}

.table {
  border: 0.5px solid rgba(0, 0, 0, 0.048);
  box-sizing: border-box;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  margin-bottom: 18px;
}

.table .table__tr {
  display: table;
}

.table .table__th,
.table .table__td {
  display: table-cell;
  padding: 20px;
  font-family: Source Sans Pro;
  font-weight: normal;
  line-height: normal;
  font-size: 14px;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 1920px) {
  .table .table__th,
  .table .table__td {
    width: calc(100%/7);
  }
}

.table .table__th[data-col="role"], .table .table__th[data-col="service"], .table .table__th[data-col="company"],
.table .table__td[data-col="role"],
.table .table__td[data-col="service"],
.table .table__td[data-col="company"] {
  text-align: left;
}

@media only screen and (max-width: 1415px) {
  .table .table__th[data-col="title"],
  .table .table__td[data-col="title"] {
    width: 100px;
  }
}

@media only screen and (max-width: 1415px) {
  .table .table__th[data-col="role"],
  .table .table__td[data-col="role"] {
    width: 130px;
  }
}

@media only screen and (max-width: 1415px) {
  .table .table__th[data-col="service"],
  .table .table__td[data-col="service"] {
    width: 130px;
  }
}

@media only screen and (max-width: 1415px) {
  .table .table__th[data-col="company"],
  .table .table__td[data-col="company"] {
    width: 150px;
  }
}

@media only screen and (max-width: 1100px) {
  .table .table__th[data-col="company"],
  .table .table__td[data-col="company"] {
    width: 130px;
  }
}

@media only screen and (max-width: 1415px) {
  .table .table__th[data-col="status"],
  .table .table__td[data-col="status"] {
    width: 170px;
  }
}

@media only screen and (max-width: 1100px) {
  .table .table__th[data-col="status"],
  .table .table__td[data-col="status"] {
    width: 150px;
  }
}

.table .table__th[data-col="date"],
.table .table__td[data-col="date"] {
  text-align: right;
}

@media only screen and (max-width: 1415px) {
  .table .table__th[data-col="date"],
  .table .table__td[data-col="date"] {
    width: 120px;
  }
}

.table .table__th[data-col="actions"],
.table .table__td[data-col="actions"] {
  text-align: right;
  padding-right: 60px;
}

@media only screen and (max-width: 1415px) {
  .table .table__th[data-col="actions"],
  .table .table__td[data-col="actions"] {
    padding-right: 30px;
    width: 130px;
  }
}

.table__thead .table__tr {
  background: #FFFFFF;
  box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.087);
  border-radius: 2px;
}

.table__thead .table__th[data-col="status"] {
  text-align: right;
}

.table .table__tr {
  width: 100%;
}

.table__tbody .table__tr {
  background-color: #fff;
}

.table__tbody .table__tr:nth-child(even) {
  background-color: #f2f2f2;
}

.table__tbody .table__td[data-col="actions"] .link-edit {
  background-color: #C2CEFF;
  font-size: 13px;
  transition: background-color .33s ease-in-out;
  margin-left: 18px;
}

@media only screen and (max-width: 1180px) {
  .table__tbody .table__td[data-col="actions"] .link-edit {
    margin-left: 10px;
  }
}

.table__tbody .table__td[data-col="actions"] .link-edit:hover {
  background-color: #3d61ff;
}

.table__tbody .table__td[data-col="actions"] .link-edit .icon {
  vertical-align: middle;
}

.table__tbody .table__td[data-col="actions"] .link-dots {
  background-color: #fff;
  font-size: 5px;
}

.table__tbody .table__td[data-col="actions"] .link-dots .icon {
  vertical-align: middle;
}

.table__tbody .table__td[data-col="actions"] .link-edit,
.table__tbody .table__td[data-col="actions"] .link-dots {
  display: inline-block;
  vertical-align: middle;
  height: 25px;
  width: 25px;
  line-height: 25px;
  border-radius: 50px;
  text-align: center;
}

.table__tbody .table__td[data-col="status"] {
  text-align: right;
}

.table__tbody .table__td[data-col="status"] .icon-circle {
  display: inline-block;
  vertical-align: middle;
  border-radius: 50px;
  width: 10px;
  height: 10px;
}

.table__tbody .table__td[data-col="status"] .status {
  margin-right: 20px;
  display: inline-block;
  vertical-align: middle;
}

.table__tbody .table__td.is-online {
  color: #86EE82;
}

.table__tbody .table__td.is-online .icon-circle {
  background-color: #86EE82;
}

.table__tbody .table__td.is-offline {
  color: #FF5867;
}

.table__tbody .table__td.is-offline .icon-circle {
  background-color: #FF5867;
}

.menu {
  font-size: 0;
}

.menu__item {
  display: block;
}

.menu__item > .menu__submenu {
  padding-left: 14px;
  padding-top: 12px;
}

.menu__item > .menu__submenu .menu__link {
  line-height: 1.15;
  font-size: 13px;
  color: #fff;
}

.menu__item:not(:last-child) {
  margin-bottom: 12px;
}

.menu__item.is-active .menu__link {
  color: #fff;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.110479);
  background-color: #2438AB;
  color: #fff;
}

.menu__item.is-active > .menu__submenu .menu__link {
  box-shadow: none;
  background-color: transparent;
}

.menu__link {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.69);
  text-decoration: none;
  transition: color .24s ease-in-out, background-color .24s ease-in-out, box-shadow .24s ease-in-out;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  line-height: 1;
  font-size: 15px;
  border-radius: 4px;
  padding: 8px 16px;
  display: block;
}

.menu__link:hover {
  color: #fff;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.110479);
  background-color: #2438AB;
}

.btn {
  cursor: pointer;
  text-decoration: none;
  box-sizing: border-box;
  font-family: Source Sans Pro;
  display: inline-block;
  transition: color .33s ease-in-out, background-color .33s ease-in-out;
  font-style: normal;
  position: relative;
}

.btn:active {
  top: 1px;
}

.btn__icon {
  margin-right: 7px;
}

.search {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 280px;
}

.search__input {
  background: #fff;
  border: 0.5px solid rgba(0, 0, 0, 0.0485734);
  box-sizing: border-box;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.0379869);
  border-radius: 2px;
  width: 100%;
  height: 44px;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  padding: 0 18px;
  line-height: normal;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.365093);
}

.search__input::placeholder {
  color: rgba(0, 0, 0, 0.137596);
}

.search__icon {
  stroke: currentColor;
  font-size: 19px;
  fill: currentColor;
  vertical-align: middle;
}

.search__btn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 44px;
  line-height: 44px;
  border-radius: 2px;
  text-align: center;
  color: #3C52C9;
  background: #fff;
  font-size: 0;
  transition: color 0.3s ease-in-out, background 0.3s ease-in-out, border 0.3s ease-in-out;
}

.input {
  border: none;
}
